import React from 'react';
import { Main, Box, Image, Button, Nav, Header, Card, CardHeader, CardBody, CardFooter, Select, DateInput, Text, TextInput } from 'grommet';
import background from '../../assets/background_home.png'
import Avatar from '@mui/material/Avatar';
import Stack from '@mui/material/Stack';
import logo from '../../assets/kupos-logo.png'
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import { styled } from '@mui/material/styles';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';
import CitysData from '../../data/home/clcitys.json'
import MyLocationIcon from '@mui/icons-material/MyLocation';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import backmid from '../../assets/backgroundmiddle.jpg'
import mediumicon from '../../assets/mediumicon.svg'
import mediumicon2 from '../../assets/mediumicon2.svg'
import mediumicon3 from '../../assets/mediumicon3.svg'
import mediumicon4 from '../../assets/mediumicon4.svg'
import homePhone from '../../assets/home-cellphones.png'
import kpay from '../../assets/kupos_pay.svg'
import busicon from '../../assets/busicon.gif'
import logobus from '../../assets/logobusticket.png';

import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import { style } from '@mui/system';

import QueryBuilderIcon from '@mui/icons-material/QueryBuilder';

import { Link } from "react-router-dom";
export const HomeScreen = ({ handleChange }) => {

    const Android12Switch = styled(Switch)(({ theme }) => ({
        padding: 8,
        '& .MuiSwitch-track': {
            borderRadius: 22 / 2,
            '&:before, &:after': {
                content: '""',
                position: 'absolute',
                top: '50%',
                transform: 'translateY(-50%)',
                width: 16,
                height: 16,
            },
            '&:before': {
                backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
                    theme.palette.getContrastText(theme.palette.primary.main),
                )}" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/></svg>')`,
                left: 12,
            },
            '&:after': {
                backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
                    theme.palette.getContrastText(theme.palette.primary.main),
                )}" d="M19,13H5V11H19V13Z" /></svg>')`,
                right: 12,
            },
        },
        '& .MuiSwitch-thumb': {
            boxShadow: 'none',
            width: 16,
            height: 16,
            margin: 2,
        },
    }));

    const BoxHome = [
        {
            color: 'transparent',
            size: "17vh",
            style: "solid",
            side: "left"
        },
        {
            color: 'transparent',
            size: "17vh",
            style: "solid",
            side: "right"
        },
        {
            color: 'transparent',
            size: "5vh",
            // style: "solid",
            side: "top"
        }
    ];

    const BoxHomeFooter = [
        {
            color: 'transparent',
            size: "17vw",
            style: "solid",
            side: "left"
        },
        {
            color: 'transparent',
            size: "17vw",
            style: "solid",
            side: "right"
        },
        {
            color: 'transparent',
            size: "5vh",
            // style: "solid",
            side: "top"
        }
    ];

    const styleBackGroundHome = {
        backgroundImage: `url(${background})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center'

    }

    const styleBackGroundHomeFooter = {
        backgroundColor: '#464647',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    }

    const styles = {
        center: {
            display: 'flex', justifyContent: 'center', alignItems: 'center', backgroundImage: `url(${backmid})`
        },
        centerLogo: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        prefooter: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        buttonTicket: {
            width: '12vh', height: '8vh', backgroundColor: '#339aca', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketCancelar: {
            width: '12vh', height: '8vh', backgroundColor: 'gray', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketQuantidade: {
            height: '4.5vh', width: '40%', backgroundColor: '#339aca', color: 'black', borderColor: 'black', border: '2px', borderRadius: '7%', textAlign: 'center'
        }
    }


    const [locationOrigin, setLocationOrigin] = React.useState()
    const [locationDestination, setLocationDestination] = React.useState('')
    const [goDate, setGoDate] = React.useState((new Date()).toISOString());
    const [backDate, setBackDate] = React.useState('');


    const [origem, setOrigem] = React.useState('Toncontin')
    const [destino, setDestino] = React.useState('Palmerola')
    const [quantidade, setQuantidade] = React.useState(1);
    const [valor, setValor] = React.useState(200);
    const [horarioIda, setHorarioida] = React.useState('06:00')
    const [horarioChegada, setHorarioChegada] = React.useState('18:00')
    const [nome, setNome] = React.useState('')
    const [sobrenome, setSobrenome] = React.useState('')
    const [documento, setDocumento] = React.useState('')

    const [pagando, setPagando] = React.useState(false);

    const handleCancelar = () => {
        setNome('');
        setSobrenome('');
        setDocumento('');
        setQuantidade(1);
        setPagando(false)
    }
    // const HandleChangeOrigin = (option) => {
    //     if (option === 'Toncontin') {
    //         setOrigem('Toncontin');
    //         setValue2('Palmerola');
    //     }
    //     else {
    //         setValue2('Toncontin');
    //         setOrigem('Palmerola');
    //     }
    // }

    const teste = () => {
        console.log('origem', origem);
        console.log('destino', destino);

    }

    const horarios = [
        '06:00',
        '06:30',
        '07:00',
        '07:30',
        '08:00',
        '08:30',
        '09:00',
        '09:30',
        '10:00',
        '10:30',
        '11:00',
        '11:30',
        '12:00',
        '12:30',
        '13:00',
        '13:30',
        '14:00',
        '14:30',
        '15:00',
        '15:30',
        '16:00',
        '16:30',
        '17:00',
        '17:30',
        '18:00',
        '18:30',
        '19:00',
        '19:30',
        '20:00',
        '20:30',
        '21:00',
        '21:30',
        '22:00',
        '22:30',
        '23:00',
        '23:30',
        '00:00',
        '00:30'];


    return (
        < Main>
            <Box style={styleBackGroundHome}>
                <Box border={BoxHome} pad="medium">
                    <Header pad="small">
                        <Image fill={false} style={{ width: '50%' }} src={logobus} />

                        <Stack direction={'row'} spacing={2} alignItems={'center'}>
                            <Button primary style={{ height: '4.5vh', backgroundColor: 'white', color: 'black', borderColor: 'transparent' }} label={'Sair'} onClick={() => { handleChange() }} />
                            {/* <Avatar>
                                <AccountCircleIcon />
                            </Avatar>

                            <FormControlLabel
                                control={<Android12Switch defaultChecked />}
                                label=""
                            /> */}
                        </Stack>
                    </Header>

                    {pagando ? (
                        <>
                            <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '15vh', marginBottom: '45vh' }}  >
                                <CardHeader>

                                </CardHeader>
                                <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Nombre</Text>
                                        <TextInput
                                            // placeholder="type here"
                                            value={nome}
                                            onChange={event => setNome(event.target.value)}
                                            size={'medium'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Apellido</Text>
                                        <TextInput
                                            // placeholder="type here"
                                            value={sobrenome}
                                            onChange={event => setSobrenome(event.target.value)}
                                            size={'medium'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>RTN / ID</Text>

                                        <TextInput
                                            // placeholder="type here"
                                            value={documento}
                                            onChange={event => setDocumento(event.target.value)}
                                            size={'medium'}
                                        />
                                    </Stack>

                                    <Button primary style={styles.buttonTicketCancelar} onClick={() => handleCancelar()} >
                                        <Stack direction={'column'} >
                                            <Text > Cancelar </Text>
                                        </Stack>
                                    </Button>

                                    <Button primary style={styles.buttonTicket} >
                                        <Stack direction={'column'} >
                                            <Text > Siguiente </Text>
                                            <Text >L. {valor}.00 </Text>
                                        </Stack>
                                    </Button>

                                </CardBody>
                            </Card>
                        </>
                    ) : (
                        <>
                            <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '15vh' }}  >
                                <CardHeader>

                                </CardHeader>
                                <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Origen</Text>
                                        <Select
                                            icon={() => (<MyLocationIcon style={{ color: '#fc8332' }} />)}
                                            options={['Toncontin', 'Palmerola']}
                                            value={origem}
                                            onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                            size={'small'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Destino</Text>
                                        <Select
                                            icon={() => (<LocationOnIcon style={{ color: '#df5a5e' }} />)}
                                            options={['Toncontin', 'Palmerola']}
                                            value={destino}
                                            onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                            size={'small'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Cantidad</Text>

                                        <Stack direction={'row'} spacing={4}>
                                            <Button primary style={styles.buttonTicketQuantidade} onClick={() => { if ((quantidade - 1) > 0) { setQuantidade(quantidade - 1); setValor((quantidade - 1) * 200) } }}>
                                                <Text style={{ textAlign: 'center' }}> -</Text>

                                            </Button>
                                            <Button primary style={styles.buttonTicketQuantidade} >
                                                <Text> {quantidade} </Text>

                                            </Button>
                                            <Button primary style={styles.buttonTicketQuantidade} onClick={() => { setQuantidade(quantidade + 1); setValor((quantidade + 1) * 200) }}>
                                                <Text> + </Text>

                                            </Button>
                                        </Stack>
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium' >Total</Text>

                                        <Stack direction={'row'} spacing={4}>
                                            <Text size='xxlarge'> L. {valor}.00</Text>

                                        </Stack>
                                    </Stack>

                                    <Button primary style={styles.buttonTicket} onClick={() => { setPagando(true) }} >
                                        <Text > Siguiente </Text>

                                    </Button>

                                </CardBody>
                            </Card>

                            <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginBottom: '24vh', marginTop: '2vh' }}  >
                                <CardHeader>

                                </CardHeader>
                                <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative', justifyContent: 'center' }}>
                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Origen</Text>
                                        <Select
                                            icon={() => (<MyLocationIcon style={{ color: '#fc8332' }} />)}

                                            options={[origem]}
                                            value={origem}
                                            // onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                            size={'small'}
                                        // disabled={true}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Horario</Text>
                                        <Stack direction={'row'} width={'100%'}>
                                            <Select
                                                icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}

                                                options={horarios}
                                                value={horarioIda}
                                                onChange={({ option }) => { setHorarioida(option) }}
                                                size={'small'}
                                            />

                                            <Select
                                                icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}

                                                options={horarios}
                                                value={horarioChegada}
                                                onChange={({ option }) => { setHorarioChegada(option) }}
                                                size={'small'}
                                            />
                                        </Stack>
                                    </Stack>
                                </CardBody>
                            </Card>
                        </>
                    )}

                </Box>

            </Box>

        </Main >
    )
};

export default HomeScreen;