import React from 'react';
import { Main, Box, Image, Button, Header, Card, CardHeader, CardBody, CardFooter, Text, TextInput } from 'grommet';
import background from '../../assets/background_home.png'

import Stack from '@mui/material/Stack';

import backmid from '../../assets/backgroundmiddle.jpg'

import logobus from '../../assets/logobusticket.png';



export const LoginSceen = ({ handleChange }) => {



    const BoxHome = [
        {
            color: 'transparent',
            size: "17vh",
            style: "solid",
            side: "left"
        },
        {
            color: 'transparent',
            size: "17vh",
            style: "solid",
            side: "right"
        },
        {
            color: 'transparent',
            size: "5vh",
            // style: "solid",
            side: "top"
        }
    ];


    const styleBackGroundHome = {
        backgroundImage: `url(${background})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center'

    }



    const styles = {
        center: {
            display: 'flex', justifyContent: 'center', alignItems: 'center', backgroundImage: `url(${backmid})`
        },
        centerLogo: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        prefooter: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        buttonTicket: {
            width: '12vh', height: '6vh', backgroundColor: '#339aca', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center', marginBottom: '3vh'
        },
        buttonTicketCancelar: {
            width: '12vh', height: '8vh', backgroundColor: 'gray', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketQuantidade: {
            height: '4.5vh', width: '40%', backgroundColor: '#339aca', color: 'black', borderColor: 'black', border: '2px', borderRadius: '7%', textAlign: 'center'
        }
    }



    const [nome, setNome] = React.useState('')
    const [sobrenome, setSobrenome] = React.useState('')


    const horarios = [
        '06:00',
        '06:30',
        '07:00',
        '07:30',
        '08:00',
        '08:30',
        '09:00',
        '09:30',
        '10:00',
        '10:30',
        '11:00',
        '11:30',
        '12:00',
        '12:30',
        '13:00',
        '13:30',
        '14:00',
        '14:30',
        '15:00',
        '15:30',
        '16:00',
        '16:30',
        '17:00',
        '17:30',
        '18:00',
        '18:30',
        '19:00',
        '19:30',
        '20:00',
        '20:30',
        '21:00',
        '21:30',
        '22:00',
        '22:30',
        '23:00',
        '23:30',
        '00:00',
        '00:30'];


    return (
        < Main>
            <Box style={styleBackGroundHome}>
                <Box border={BoxHome} pad="medium">





                    <Header pad="small">
                        <Image fill={false} style={{ width: '50%' }} src={logobus} />

                        {/* <Stack direction={'row'} spacing={2} alignItems={'center'}>
                            <Button primary style={{ height: '4.5vh', backgroundColor: 'white', color: 'black', borderColor: 'transparent' }} label={'My Tickets'} />
                            <Avatar>
                                <AccountCircleIcon />
                            </Avatar>

                            <FormControlLabel
                                control={<Android12Switch defaultChecked />}
                                label=""
                            />
                        </Stack> */}
                    </Header>




                    <Box align="center" justify="center" style={{ marginBottom: '32vh', marginTop: '10vh' }}>
                        <Card height="medium" width="medium" background="light-1">
                            <CardHeader pad="medium">
                                <Text>Busticket Login </Text>
                            </CardHeader>
                            <CardBody pad="medium" align="center" justify="center">
                                <Stack direction={'column'} width={'100%'} >
                                    <Text size='medium'>Login</Text>
                                    <TextInput
                                        // placeholder="type here"
                                        value={nome}
                                        onChange={event => setNome(event.target.value)}
                                        size={'medium'}
                                    />
                                </Stack>

                                <Stack direction={'column'} width={'100%'}>
                                    <Text size='medium'>Contrasea</Text>
                                    <TextInput
                                        // placeholder="type here"
                                        type='password'
                                        value={sobrenome}
                                        onChange={event => setSobrenome(event.target.value)}
                                        size={'medium'}
                                    />
                                </Stack>
                            </CardBody>
                            <CardFooter pad={{ horizontal: "small" }} background="light-2" align="center" justify="center">
                                <Button primary style={styles.buttonTicket} onClick={() => { handleChange() }} >

                                    <Text > Login </Text>

                                </Button>
                            </CardFooter>
                        </Card>
                    </Box>






















                </Box>
            </Box>
        </Main >
    )
};

export default LoginSceen;